* 	{box-sizing: border-box;}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.main-nav{
	text-align: right;
	margin-bottom: 50px;
	padding: 0 25px;
}
a{
	color: #4D8FAC;
	text-decoration: none;
	-webkit-transition: all .25s ease;
	transition: all .25s ease;
}
a:hover{
	color: #5D8CAE;
	text-decoration: underline;
}
.main-nav a{
	display: inline-block;
	padding: 25px 25px;
}
.home--container{
	margin: 0 auto;
	width: 600px;
}
.home--container h1{
	text-align: center;
}

.chat-list{
	margin: 0; padding: 0;
	list-style-type: none;
}
.chat-list li{
	padding: 5px 0;
}
.chat-list em{
	font-size: 12px;
	font-style: normal;
}
.chat-list strong{
	display: inline-block;
	margin: 0 5px 0 10px;
}
.send-chat{
	margin-top: 15px;
}
.send-chat input{
	border: 1px solid #ccc;
	display: block;
	width: 100%;
	padding: 10px;
	font-size: 16px;
	outline: none;
}
.send-chat input:focus{
	border: 1px solid black;
}
.disallow-chat{
	text-align: center;
}
.auth--container{
	max-width: 500px;
	width: 95%;
	margin: 0 auto;
}
.auth--container{
	text-align: center;
}
.auth--container label{
	display: block;
	text-align: left;
}
.auth--container input{
	border: none;
	border-bottom: 1px solid #ccc;
	display: block;
	width: 100%;
	padding: 15px;
	font-size: 14px;
	margin-bottom: 10px;
	outline: none;
}
.auth--container input:focus{
	border-bottom: 1px solid #000;
}
.auth--container button{
	display: inline-block;
	padding: 15px 35px;
	color: #fff;
	background: #4D8FAC;
	-webkit-transition: background .25s ease;
	transition: background .25s ease;
	cursor: pointer;
	border: none;
}
.auth--container button:hover{
	background: #5D8CAE;
}
